html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.grid--fixed {
  max-width: 100%;
  width: 1200px;
  margin: 0 auto;
}
.grid--flex-cols .grid__col {
  display: flex;
}
.grid__row {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
